<template>
  <div>
    <div class="operations" style="height: 50vh"></div>
  </div>
</template>

<script>
import * as d3 from "d3";
import G2 from "@antv/g2";
import { View } from "@antv/data-set";

export default {
  name: "PageAnalyze",
  data() {
    return {
      operationsVis: {},
      operationsChart: {},
      chart: {}
    };
  },
  computed: {
    codemirror() {
      return this.$refs.myCm.codemirror;
    },
    operations() {
      return this.$store.state.operations;
    }
  },
  mounted() {
    // data: ["click", "brush", "drag", "mouseover", "invertBrush", "zoom"]

    this.chart = new G2.Chart({
      container: document.getElementsByClassName("operations")[0],
      forceFit: true,
      height: 400,
      theme: "dark"
      // renderer : 'svg'
    });

    const defs = {
      time: {
        type: "time", //  time 
        mask: "HH:mm:ss" // 
      },
      action: {
        type: "cat", //  cat 
        values: this.$store.state.operationTypes //  c 
      }
    };
    this.chart.source(this.operations, defs);
    this.chart.legend({
      title: null, // 
      marker: "square" //  marker 
    });
    this.chart.legend("nodes", false); //  nodes 
    this.chart.tooltip({
      showTitle: false
      // itemTpl: '<li>{color}{name}\t{value}</li>'
    });
    this.chart
      .point()
      .position("time*action")
      .color("action")
      .size("nodes", nodes => {
        let size = Math.sqrt(nodes.length);
        return size > 4.5 ? size : 4.5;
      })
      .opacity(0.8)
      .shape("circle")
      .tooltip("time*action", (time, action, nodes) => {
        return { name: action, value: time + "%" };
        // , { "": action }];
      });
    this.chart.render();
  },
  activated() {
    this.chart.changeData(this.operations);
  },
  methods: {}
};
</script>